.file-edit-content{width:100%;background:#fff;padding-bottom:20px}.file-edit-content>h2{line-height:60px;text-align:center;padding-bottom:6px;position:relative}.file-edit-content>h2 .down{position:absolute;right:0;top:0}.file-edit-content>h2 .down>i{font-size:20px;color:#2d8cf0;cursor:pointer;vertical-align:middle}.file-edit-content>h2 .down em{font-size:12px;font-style:normal;vertical-align:middle;color:#777;margin-left:-2px}.file-edit-content .code-mirror-model{height:calc(100vh - 300px)}.file-edit-content .code-mirror-model .cm-s-mdn-like.CodeMirror{height:calc(100vh - 310px)}.file-edit-content .submit-c{text-align:center;padding-top:12px}.file-operation{padding:30px 0;text-align:center}